:root {
  --test: #fb7777;
  --bg: #e4e4e4;
  --thin: 100;
  --extra-light: 200;
  --reqular: 400;
  --semi-bold: 600;
  --bold: 700;
  --font-color: #353535;
  --white: #f7f7f7;
  --bbblue: #3f51b5;
}
* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
  /* overflow: hidden; */
}

body {
  background: #e4e4e4;
  background: var(--bg);
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-weight: var(--reqular);
  background-color: #e4e4e4;
  background-color: var(--bg);
  box-sizing: border-box;
}

a {
  color: #353535;
  color: var(--font-color);
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: #353535;
  color: var(--font-color);
  font-weight: 600;
  font-weight: var(--semi-bold);
}
/* // Main Layout Design start */
header {
  width: 100%;
  height: 50px;
  background: #3f51b5;
  background: var(--bbblue);
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #f7f7f7;
  color: var(--white);
  position: fixed;
  z-index: 11;
}

div.logo {
  width: 235px;
  text-align: center;
  font-weight: 600;
  font-weight: var(--semi-bold);
  letter-spacing: 1.5px;
  padding-top: 12px;
}

div.logo > div.hamburger {
  display: none;
}

div.menu ul {
  list-style: none;
  cursor: pointer;
  margin-top: 12px;
}

div.menu ul li span {
  padding-right: 30px;
  font-size: 25px;
  color: #f7f7f7;
  color: var(--white);
  cursor: pointer;
}

.menu {
  position: relative;
}

.user-menu-header {
  position: absolute;
  width: 235px;
  height: 145px;
  background: #f7f7f7;
  background: var(--white);
  top: 40px;
  right: 10px;
  border: 1px solid #b6b1b1;
  color: #353535;
  color: var(--font-color);
  box-shadow: 1px 8px 18px 4px rgba(0, 0, 0, 0.24);
}

.user-menu-header a {
  display: block;
  width: 100%;
  height: 45px;
  line-height: 45px;
  border-bottom: 1px solid #ddd;
  padding: 0px 15px;
}

.user-menu-header a:hover {
  background: #efefef;
  transition: all 0.3s ease-in-out;
}

.mainContentWrapper {
  padding-top: 50px;
  min-height: 100vh;
}

.sidebar {
  width: 235px;
  background: #f7f7f7;
  background: var(--white);
  min-height: 100vh;
  z-index: 10;
  position: fixed;
  left: 0;
  top: 50px;
  max-height: calc(100vh - 9rem);
  overflow-y: auto;
}

/* // Main Layout Design end */
/* @override css  */
.pro-sidebar {
  width: 235px;
  min-width: 235px;
}

.pro-menu-item {
  color: #353535 !important;
  color: var(--font-color) !important;
}
nav.pro-menu ul li {
  border-bottom: 1px solid #ddd;
}
div.react-slidedown ul li {
  border-bottom: none !important;
}

/* // sidebar Design start */
.sidebar-menu {
  color: #353535;
  color: var(--font-color);
  font-weight: var(--regular);
}
/* // sidebar Design end */

/* // Main Content start */
main {
  margin-left: 235px;
  padding: 10px 10px;
  transition: all 0.3s ease-in-out;
}

.sideber-footer {
  height: 50px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  line-height: 1.8;
  font-size: 12px;
}

/* // overlay  */
.overlay {
  position: fixed;
  left: 0;
  top: 50px;
  background: #414141;
  width: 100%;
  height: 100%;
  opacity: 0.5;
  z-index: 1;
}

.show {
  display: block;
}
.hide {
  display: none;
}

/* // user info sidebar  */
.sidebar-userinfo {
  height: 135px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  color: #353535;
  color: var(--font-color);
  padding-top: 15px;
}

.sidebar-userinfo p.user-roll {
  font-weight: 600;
  font-weight: var(--semi-bold);
  margin-bottom: 0px;
}

.sidebar-userinfo div {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.sidebar-userinfo p.user-name {
  padding: 5px 0px;
  margin-bottom: 0rem;
}
.sidebar-userinfo img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
}

/* // Main Content end */

@media screen and (min-width: 320px) and (max-width: 380px) {
  .dashboard {
    width: 375px;
  }
}
@media screen and (min-width: 100px) and (max-width: 600px) {
  main {
    margin-left: 0px;
    padding: 0px 0px;
  }
  /* // sidebar open close  */
  .open {
    left: 0 !important;
  }

  .close {
    left: -235px !important;
  }

  .sidebar {
    top: 50;
    left: -235px;
    transition: all 0.1s ease-in-out;
    z-index: 2;
  }

  div.logo {
    width: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding-top: 12px;
  }

  div.logo p {
    width: 100%;
    text-align: center;
  }

  div.logo > div.hamburger {
    display: block;
    padding-right: 30px;
    cursor: pointer;
    margin-top: 0px;
    font-size: 22px;
  }

  div.menu {
    display: none;
  }

  .collapse-btn {
    display: none;
  }

  .sideber-footer {
    height: 100px;
    padding-bottom: 50px;
  }

  .dashboard-todays-statistics,
  .summery-activity-log,
  .dashboard-branches {
    padding: 0px 0px !important;
  }

  .branchContent {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
  }

  .branchContent div {
    padding: 5px;
  }

  .branchContent div:first-child {
    font-size: 14px;
  }
  .branchContent div:first-child {
    order: 1;
  }

  .summery .font-16 {
    font-size: 14px;
  }

  .phoneScreenTitle {
    padding-top: 20px;
    padding-bottom: 0px;
    font-size: 15px;
  }

  .phoneScreenTitle h3 {
    padding-left: 10px;
  }
}

/* default scrollbar css start */
::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  background: rgb(182, 182, 182);
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: #f7f7f7;
  background: var(--white);
}

/* scrollbar Css end  */

.collapse-btn {
  position: fixed;
  left: 200px;
  top: 50px;
  font-size: 26px;
  z-index: 11;
  color: #c0c0c0;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}
.collapse-btn:hover {
  color: #353535;
  color: var(--font-color);
}

.copyright,
.version {
  margin-bottom: 0;
}

.pro-sidebar > .pro-sidebar-inner > .pro-sidebar-layout .pro-sidebar-footer {
  border-top: none;
}

/* =================================================================
============================ / Indibidual Page Css / ===============
================================================================= */
