.login_wrapper {
  min-height: 100vh;
  background: linear-gradient(to right top, #5680e9, #8860dc);
  display: flex;
  justify-content: center;
  align-items: center;
}

.glass {
  display: flex;
  background: white;
  min-height: 80vh;
  width: 60%;
  background: linear-gradient(
    to right bottom,
    rgba(255, 255, 255, 0.4),
    rgba(255, 255, 255, 0.1)
  );
  border-radius: 1em;
  z-index: 2;
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
}

.circle1,
.circle2 {
  position: absolute;
  height: 200px;
  width: 200px;
  background: white;
  background: linear-gradient(
    to right bottom,
    rgba(255, 255, 255, 0.8),
    rgba(255, 255, 255, 0.1)
  );
  z-index: 1;
  border-radius: 50%;
}
.circle1 {
  top: 5%;
  right: 15%;
}
.circle2 {
  bottom: 5%;
  left: 15%;
}

.left {
  flex: 1 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: rgba(255, 255, 255, 0.4);
  border-radius: 1em;
}

#icon {
  display: flex;
  margin-bottom: 30px;
}
#icon h2 {
  margin-left: 10px;
  font-size: 50px;
  background: -webkit-linear-gradient(#8860dc, #5680e9);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.welcome p {
  text-align: center;
