/* social icon start */
.social {
  display: flex;
  padding-top: 10px;
}
.social .button {
  color: #000;
  display: inline-block;
  height: 40px;
  width: 40px;
  /* float: left; */
  margin: 10px 0px;
  overflow: hidden;
  background: #fff;
  border-radius: 50px;
  cursor: pointer;
  box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease-out;
  cursor: pointer;
}
.social .button:hover {
  width: 130px;
}

input[type="submit"] {
  cursor: pointer;
}

.social .button .icon {
  display: inline-block;
  height: 40px;
  width: 40px;
  text-align: center;
  border-radius: 50px;
  box-sizing: border-box;
  line-height: 40px;
  transition: all 0.3s ease-out;
}
/* icon color start */
.social .button:nth-child(1):hover .icon {
  background: #4267b2;
}
.social .button:nth-child(2):hover .icon {
  background: #1da1f2;
}
.social .button:nth-child(3):hover .icon {
  background: #f73e2f;
}
.social .button:nth-child(4):hover .icon {
  background: #0073b1;
}
/* icon color end */
.social .button {
  font-size: 15px;
  font-weight: 500;
  line-height: 40px;
  margin-left: 10px;
}
.social .button .icon i {
  transition: all 0.3s ease-out;
}
.social .button:hover .icon i {
  color: #fff;
}
/* icon name color */
.social .button:nth-child(1) span {
  color: #4267b2;
}
.social .button:nth-child(2) span {
  color: #1da1f2;
}
.social .button:nth-child(3) span {
  color: #f73e2f;
}
.social .button:nth-child(4) span {
  color: #0073b1;
}

/* icon name color end */
/* social icon end */
.responsive {
  display: flex;
}

