/* responsive start */
@media screen and (min-width: 901px) {
  .responsive {
    display: none;
  }
}
@media screen and (max-width: 900px) {
  .main {
    background: linear-gradient(to right top, #5680e9, #8860dc);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  .glass {
    width: 500px;
  }
  .circle1,
  .circle2 {
    display: none;
  }
  .left {
    display: none;
  }
  .responsive .res-icon span {
    display: flex;
    flex-direction: column;
    font-size: 25px;
    font-weight: 1000;
    background: -webkit-linear-gradient(#8860dc, #5680e9);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
}

